import React, { useState } from 'react';
import { Link, useLocation, useSearchParams, Navigate } from 'react-router-dom';
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Grid,
  makeStyles,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Header from '../components/header';
import Footer from '../components/footer';

const TRANSITION = 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)';

const PLANS = {
  starter: { id: 'starter', name: 'Starter', price: '2,000', color: '#22c55e' },
  professional: { id: 'professional', name: 'Professional', price: '6,000', color: '#3b82f6' },
  enterprise: { id: 'enterprise', name: 'Enterprise', price: '13,000', color: '#8b5cf6' },
};

const useStyles = makeStyles((theme) => ({
  '@keyframes fadeIn': {
    from: { opacity: 0, transform: 'translateY(12px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  },
  '@keyframes fadeInUp': {
    from: { opacity: 0, transform: 'translateY(20px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  },
  root: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
    background: 'linear-gradient(180deg, #fafafa 0%, #f5f5f5 30%, #f0f0f0 70%, #f5f5f5 100%)',
  },
  main: {
    flex: 1,
    paddingTop: 100,
    paddingBottom: theme.spacing(10),
    animation: '$fadeIn 0.5s ease-out',
    [theme.breakpoints.down('sm')]: { paddingTop: 88, paddingBottom: theme.spacing(6) },
    [theme.breakpoints.between('sm', 'md')]: { paddingTop: 96 },
  },
  container: {
    maxWidth: 900,
    margin: '0 auto',
    padding: theme.spacing(3, 2),
    width: '100%',
    boxSizing: 'border-box',
    [theme.breakpoints.down('sm')]: { padding: theme.spacing(2, 1.5) },
  },
  backLink: {
    display: 'inline-flex',
    alignItems: 'center',
    gap: theme.spacing(0.5),
    color: '#64748b',
    textDecoration: 'none',
    marginBottom: theme.spacing(3),
    fontSize: '0.9375rem',
    fontWeight: 500,
    transition: TRANSITION,
    '&:hover': { color: '#ea580c' },
  },
  title: {
    fontWeight: 700,
    fontSize: '1.875rem',
    color: '#0f172a',
    marginBottom: theme.spacing(1),
    letterSpacing: '-0.025em',
    [theme.breakpoints.down('sm')]: { fontSize: '1.5rem' },
  },
  subtitle: {
    color: '#64748b',
    fontSize: '0.9375rem',
    marginBottom: theme.spacing(4),
    lineHeight: 1.6,
  },
  layout: {
    display: 'grid',
    gridTemplateColumns: '1fr 380px',
    gap: theme.spacing(4),
    alignItems: 'start',
    [theme.breakpoints.down('md')]: { gridTemplateColumns: '1fr' },
  },
  formPaper: {
    padding: theme.spacing(4, 3),
    borderRadius: 16,
    [theme.breakpoints.down('sm')]: { padding: theme.spacing(2.5, 2) },
    boxShadow: '0 2px 12px rgba(0,0,0,0.04), 0 1px 2px rgba(0,0,0,0.04)',
    border: '1px solid rgba(234, 88, 12, 0.12)',
    borderTop: '3px solid #ea580c',
    background: '#fff',
    animation: '$fadeInUp 0.4s ease-out',
  },
  formTitle: {
    fontWeight: 600,
    fontSize: '1.125rem',
    color: '#0f172a',
    marginBottom: theme.spacing(3),
    paddingBottom: theme.spacing(2),
    borderBottom: '1px solid #eee',
  },
  field: {
    marginBottom: theme.spacing(2),
    '& .MuiOutlinedInput-root': {
      borderRadius: 8,
      transition: TRANSITION,
    },
  },
  orderPaper: {
    padding: theme.spacing(3),
    borderRadius: 16,
    position: 'sticky',
    top: 120,
    [theme.breakpoints.down('md')]: { position: 'static' },
    boxShadow: '0 2px 12px rgba(0,0,0,0.04)',
    border: '1px solid rgba(0,0,0,0.06)',
    background: '#fff',
    animation: '$fadeInUp 0.4s ease-out 0.1s both',
  },
  orderTitle: {
    fontWeight: 600,
    fontSize: '1rem',
    color: '#0f172a',
    marginBottom: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    borderBottom: '1px solid #eee',
  },
  planBadge: {
    display: 'inline-block',
    padding: theme.spacing(0.5, 1.5),
    borderRadius: 8,
    color: '#fff',
    fontWeight: 600,
    fontSize: '0.875rem',
    marginBottom: theme.spacing(1),
  },
  planName: {
    fontWeight: 600,
    fontSize: '1.25rem',
    color: '#111',
    marginBottom: theme.spacing(1),
  },
  planPrice: {
    fontWeight: 700,
    fontSize: '1.5rem',
    color: '#0f172a',
    marginBottom: theme.spacing(2),
  },
  orderTotal: {
    paddingTop: theme.spacing(2),
    borderTop: '2px solid #eee',
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginTop: theme.spacing(2),
  },
  submitButton: {
    width: '100%',
    marginTop: theme.spacing(3),
    background: 'linear-gradient(135deg, #ea580c 0%, #f97316 100%)',
    color: '#fff',
    fontWeight: 600,
    padding: theme.spacing(1.5, 4),
    textTransform: 'none',
    borderRadius: 10,
    boxShadow: '0 4px 14px rgba(234, 88, 12, 0.35)',
    transition: TRANSITION,
    '&:hover': {
      background: 'linear-gradient(135deg, #c2410c 0%, #ea580c 100%)',
      boxShadow: '0 6px 20px rgba(234, 88, 12, 0.4)',
      transform: 'translateY(-1px)',
    },
  },
  successBox: {
    textAlign: 'center',
    padding: theme.spacing(6, 3),
    animation: '$fadeInUp 0.5s ease-out',
  },
  successTitle: {
    fontWeight: 700,
    fontSize: '1.5rem',
    color: '#0f172a',
    marginBottom: theme.spacing(1),
  },
}));

function CheckoutPurchase() {
  const classes = useStyles();
  const location = useLocation();
  const [searchParams] = useSearchParams();
  const planId = searchParams.get('plan') || location.state?.plan?.id;
  const planFromState = location.state?.plan;

  const plan = planId
    ? (PLANS[planId] || planFromState)
    : planFromState || null;

  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    company: '',
    phone: '',
    billingAddress: '',
    paymentMethod: 'bank_transfer',
  });
  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitted(true);
  };

  if (!plan) {
    return <Navigate to="/checkout" replace />;
  }

  if (submitted) {
    return (
      <div className={classes.root}>
        <Header />
        <main className={classes.main}>
          <Box className={classes.container}>
            <Box className={`${classes.formPaper} ${classes.successBox}`}>
              <Box style={{ fontSize: 64, color: '#22c55e', marginBottom: 16 }}></Box>
              <Typography className={classes.successTitle}>Order Received</Typography>
              <Typography className={classes.subtitle} style={{ marginBottom: 0 }}>
                Thank you for subscribing to UlapPayroll {plan.name} Plan. We&apos;ll send payment
                instructions to <strong>{formData.email}</strong> within 24 hours. Your account will
                be activated once payment is confirmed.
              </Typography>
              <Button
                component={Link}
                to="/ulapayroll"
                variant="contained"
                className={classes.submitButton}
                style={{ marginTop: 32, maxWidth: 240 }}
              >
                Back to Home
              </Button>
            </Box>
          </Box>
        </main>
        <Footer />
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <Header />
      <main className={classes.main}>
        <Box className={classes.container}>
          <Link to="/checkout" className={classes.backLink}>
            <ArrowBackIcon fontSize="small" />
            Change plan
          </Link>
          <Typography className={classes.title}>Complete Your Purchase</Typography>
          <Typography className={classes.subtitle}>
            Enter your billing details to subscribe to UlapPayroll.
          </Typography>

          <form onSubmit={handleSubmit}>
            <Box className={classes.layout}>
              <Paper className={classes.formPaper} elevation={0}>
                <Typography className={classes.formTitle}>Billing Information</Typography>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      required
                      label="Full Name"
                      name="fullName"
                      value={formData.fullName}
                      onChange={handleChange}
                      variant="outlined"
                      className={classes.field}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      required
                      label="Work Email"
                      name="email"
                      type="email"
                      value={formData.email}
                      onChange={handleChange}
                      variant="outlined"
                      className={classes.field}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      required
                      label="Company Name"
                      name="company"
                      value={formData.company}
                      onChange={handleChange}
                      variant="outlined"
                      className={classes.field}
                    />
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <TextField
                      fullWidth
                      label="Phone Number"
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                      variant="outlined"
                      className={classes.field}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      fullWidth
                      label="Billing Address"
                      name="billingAddress"
                      value={formData.billingAddress}
                      onChange={handleChange}
                      variant="outlined"
                      multiline
                      rows={2}
                      placeholder="Street, City, Province, Postal Code"
                      className={classes.field}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl variant="outlined" fullWidth className={classes.field}>
                      <InputLabel>Payment Method</InputLabel>
                      <Select
                        name="paymentMethod"
                        value={formData.paymentMethod}
                        onChange={handleChange}
                        label="Payment Method"
                      >
                        <MenuItem value="bank_transfer">Bank Transfer</MenuItem>
                        <MenuItem value="gcash">GCash</MenuItem>
                        <MenuItem value="bpi">BPI</MenuItem>
                        <MenuItem value="bdo">BDO</MenuItem>
                      </Select>
                    </FormControl>
                    <Typography variant="caption" color="textSecondary" display="block">
                      Payment instructions will be sent to your email after confirmation.
                    </Typography>
                  </Grid>
                </Grid>
              </Paper>

              <Paper className={classes.orderPaper} elevation={0}>
                <Typography className={classes.orderTitle}>Order Summary</Typography>
                <span
                  className={classes.planBadge}
                  style={{ background: plan.color }}
                >
                  {plan.name}
                </span>
                <Typography className={classes.planName}>{plan.name} Plan</Typography>
                <Typography className={classes.planPrice}>{plan.price}/month</Typography>
                <Box className={classes.orderTotal}>
                  <Typography style={{ fontWeight: 700, fontSize: '1.125rem' }}>
                    Total
                  </Typography>
                  <Typography style={{ fontWeight: 700, fontSize: '1.25rem', color: plan.color }}>
                    {plan.price}/mo
                  </Typography>
                </Box>
                <Button
                  type="submit"
                  variant="contained"
                  className={classes.submitButton}
                  size="large"
                >
                  Confirm Purchase
                </Button>
              </Paper>
            </Box>
          </form>
        </Box>
      </main>
      <Footer />
    </div>
  );
}

export default CheckoutPurchase;
